* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background-color:rgb(227, 237, 240);
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

input:focus {
  color: rgb(15, 15, 238);
}

.country-list, .country-info {
  padding: 0;
  margin: 0;
  list-style: none;
}

.country-list li {
  display: flex;
}

.country {
  display: flex;
  font-size: 26px;
}

p {
  margin-left: 10px;
}